const words = text.split(" ");

const categories = ["Men", "Women", "Kids", "Sale"];

export const getRandomStr = (numberOfWords = 1) => {
  let nextWords = words.splice(-numberOfWords, numberOfWords);

  if (!nextWords.length)
    nextWords = Array.from(Array(numberOfWords)).map(() => "Lorem");

  nextWords = nextWords.map((item) => {
    let str = item;

    while (str.length < 3 && words.length) str += ` ${words.pop()}`;

    return str;
  });

  const str = capitalizeFirstLetter(nextWords.join(" "));

  return str;
};

const getRandomStrArray = (
  arrayLength: number,
  numberOfWords = 1,
  randomNumberOfWords?: number
) => {
  const arr = Array.from(Array(arrayLength));

  return arr.map(() => {
    let str = getRandomStr(
      randomNumberOfWords
        ? numberOfWords + Math.floor(0.5 + Math.random() * randomNumberOfWords)
        : numberOfWords
    );

    return str;
  });
};

export const navigationData = categories.map((category, index) => {
  return {
    id: index + 1,
    title: category,
    subCategories: Array.from(
      Array(Math.ceil(2 + Math.random() * 3)).keys()
    ).map((subCategory) => ({
      id: subCategory,
      title: getRandomStr(),
      items: Array.from(Array(Math.ceil(5 + Math.random() * 5)).keys()).map(
        (item) => ({
          id: item,
          title: getRandomStr(),
        })
      ),
    })),
  };
});

